<template>
  <div id="table">
  <el-table
    :data="tableData"
    style="width: 100%"
    :default-sort = "{prop: 'time', order: 'descending'}">
    <el-table-column
      prop="name"
      label="CLIENT"
      sortable
      width="180">
    </el-table-column>
    <el-table-column
      prop="time"
      label="UPDATED"
      sortable
      width="180">
    </el-table-column>
    <el-table-column
      prop="entry"
      label="ENTRIES"
      sortable
      width="140"
      class-name="entry">
    </el-table-column>
    <el-table-column
      label="MOOD"
      :formatter="formatter">
      <template slot-scope="scope">
        <img src="@/assets/emoji/emoji_good.png">
        <span style="margin-left: 2px; margin-right: 10px;">{{ scope.row.good }}</span>
        <img src="@/assets/emoji/emoji_neutral.png">
        <span style="margin-left: 2px; margin-right: 10px;">{{ scope.row.neutral }}</span>
        <img src="@/assets/emoji/emoji_bad.png">
        <span style="margin-left: 2px; margin-right: 10px;">{{ scope.row.bad }}</span>
      </template>
    </el-table-column>
    <el-table-column
      prop="alert"
      label="ALERTS"
      sortable
      width="100">
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [{
          name: 'John V. Doe',
          time: '2 days ago',
          entry: '20',
          good: '2',
          neutral: '3',
          bad: '4',
          alert: '0'
        }, {
          name: 'Sarah J. Malindo',
          time: '4 days ago',
          entry: '6',
          good: '2',
          neutral: '3',
          bad: '4',
          alert: '2'
        }, {
          name: 'Chris D. Lindeman',
          time: '4 days ago',
          entry: '3',
          good: '2',
          neutral: '3',
          bad: '4',
          alert: '0'
        }, {
          name: 'Zachary Dean',
          time: '1 week ago',
          entry: '6',
          good: '2',
          neutral: '3',
          bad: '4',
          alert: '6'
        }]
      }
    },
    methods: {
      formatter(row, column) {
        return row.address;
      }
    }
  }
</script>

<style>
  .entry {
    text-align: center !important;
  }
  .el-table img {
    height: 16px;
  }
  .el-table__body tr,
    .el-table__body td {
      padding: 0;
      height: 60px;
  }
#table {
}
</style>


